import React, { useState, useEffect } from "react";
// import { tableSample } from "../utils/fakeData";
import Tr from "./Tr";
import { paginate } from "../../PageNav/paginate";

export default function Tbody({ headers, searchState, pageSize, currentPage }) {
  // const rowDatas = tableSample.data;
  const [data, setData] = useState([]);

  useEffect(() => {
    async function fetchData() {
      try {
        const response = await fetch(
          `http://my-json-server.typicode.com/MrJeyhun/fkserver4CP/data`
        );
        const json = await response.json();
        setData(json);
      } catch (e) {
        console.error(e);
      }
    }
    fetchData();
  }, []);

  const paginateData = paginate(data, currentPage, pageSize);

  return (
    <tbody>
      {paginateData.map((item, i) => {
        return (
          item[0].toLowerCase().includes(searchState) && (
            <Tr headers={headers} key={i} bodyTr={true}>
              {item}
            </Tr>
          )
        );
      })}
    </tbody>
  );
}
