import React from "react";
import Td from "./Td";

export default function Tr({ children, headers, bodyTr }) {
  return (
    <tr>
      {children.map(
        (item, i) =>
          headers &&
          headers[i].show && (
            <Td key={i} header={headers[i].header}>
              {item}
            </Td>
          )
      )}
      {bodyTr ? (
        <td className="actionBtns">
          <button className="actionBtns--edit">Edit</button>
          <button className="actionBtns--delete">Delete</button>
        </td>
      ) : (
        <td className="actionsTitle">Actions</td>
      )}
    </tr>
  );
}
