import React from "react";

export default function PageNav({
  itemsCount,
  pageSize,
  currentPage,
  handlePageChange
}) {
  let pagesCountArray = [];
  let pagesCount = Math.ceil(itemsCount / pageSize);
  for (var i = 1; i <= pagesCount; i++) {
    pagesCountArray.push(i);
  }
  return (
    <div>
      <nav>
        <div className="pagination">
          {pagesCountArray.map(pageNumber => (
            <div
              key={pageNumber}
              className={
                pageNumber === currentPage ? "page-item active" : "page-item"
              }
            >
              <a
                className="page-link"
                onClick={() => handlePageChange(pageNumber)}
              >
                {pageNumber}
              </a>
            </div>
          ))}
        </div>
      </nav>
    </div>
  );
}
