import React from "react";
import "./Checkbox.scss";

export default function Checkbox({ id, handleCheck, isChecked, header }) {
  return (
    <React.Fragment>
      <label htmlFor={id}>
        <label className="checkbox--styled">
          <input
            id={id}
            type="checkbox"
            checked={isChecked}
            onChange={handleCheck}
          />
          <span />
        </label>
        {header}
      </label>
    </React.Fragment>
  );
}
