import React, { useState } from "react";
import "./Table.scss";
import TableBar from "./TableBar/TableBar";
import TableInner from "./TableInner/TableInner";
import { tableSample } from "./utils/fakeData";
import PageNav from "../PageNav/PageNav";

export default function Table({ data = tableSample, searchState }) {
  const initialStateHeaders = data.headers.map((header, i) => ({
    header,
    id: i,
    show: true
  }));

  const [headers, setHeaders] = useState(initialStateHeaders);

  const handleCheck = ({ currentTarget }) => {
    const newState = headers.map(item =>
      item.id == currentTarget.id ? { ...item, show: !item.show } : null
    );
    setHeaders(newState);
  };

  //??
  const titleOfTable = "Customer Reports";

  const [pageSize, setPageSize] = useState(8);
  const [currentPage, setCurrentPage] = useState(1);
  const handlePageChange = pageNum => {
    setCurrentPage(pageNum);
  };

  return (
    <div className="table--container">
      <TableBar
        titleOfTable={titleOfTable}
        headers={headers}
        handleCheck={handleCheck}
      />
      <TableInner
        headers={headers}
        searchState={searchState}
        currentPage={currentPage}
        pageSize={pageSize}
        handlePageChange={handlePageChange}
      />
      <PageNav
        itemsCount={12}
        currentPage={currentPage}
        pageSize={pageSize}
        handlePageChange={handlePageChange}
      />
    </div>
  );
}
