import React, { useState } from "react";
import Checkbox from "../Checkbox/Checkbox";
import "../ModalFilter/ModalFilter.scss";

export default function ModalFilter({ headers, handleCheck }) {
  const [show, setShow] = useState(false);

  function handleClick() {
    setShow(!show);
  }

  return (
    <React.Fragment>
      <span className="filter--icon" onClick={handleClick}>
        <svg
          viewBox="64 64 896 896"
          focusable="false"
          className=""
          data-icon="filter"
          width="1em"
          height="1em"
          fill="currentColor"
          aria-hidden="true"
        >
          <path d="M349 838c0 17.7 14.2 32 31.8 32h262.4c17.6 0 31.8-14.3 31.8-32V642H349v196zm531.1-684H143.9c-24.5 0-39.8 26.7-27.5 48l221.3 376h348.8l221.3-376c12.1-21.3-3.2-48-27.7-48z" />
        </svg>
      </span>
      {show ? (
        <div className="modal--filter">
          {headers.map((item, i) => (
            <li key={i}>
              <Checkbox
                id={item.id}
                key={item.id}
                header={item.header}
                isChecked={item.show}
                handleCheck={handleCheck}
              />
            </li>
          ))}
        </div>
      ) : null}
    </React.Fragment>
  );
}
