import React from "react";
import Th from "./Th";
import Tr from "./Tr";

export default function Thead({ headers }) {
  return (
    <thead>
      <Tr headers={headers} bodyTr={false}>
        {headers.map(
          (item, i) => item.show && <Th key={item.id}>{item.header}</Th>
        )}
      </Tr>
    </thead>
  );
}
